.homeDiv {
  position: relative;
}


@media (min-width: 1250px) {
  .homeHeader {
    font-size: 40px;
    position: relative;
    right: 35%;
    top: 20px;
    color: #888888;
  }
}


@media (max-width: 1250px) {
  .homeHeader {
    font-size: 34px;
    position: relative;
    right: 35%;
    top: 20px;
    color: #888888;
  }
}

@media (max-width: 1060px) {
  .homeHeader {
    font-size: 30px;
    position: relative;
    right: 35%;
    top: 20px;
    color: #888888;
  }
}

@media (max-width: 945px) {
  .homeHeader {
    font-size: 26px;
    position: relative;
    right: 35%;
    top: 20px;
    color: #888888;
  }
}

@media (max-width: 820px) {
  .homeHeader {
    font-size: 24px;
    position: relative;
    right: 35%;
    top: 20px;
    color: #888888;
    right: 0%;
  }
}

@media (max-width: 767px) {
  .introText {
    position: relative;
    width: 80%;
  }
}

@media (max-width: 500px) {
  .introText {
    position: relative;
    max-width: 400px;
    min-width: 250px;
  }
}